<div class="pt-2 ps-1 mb-3" v-if="!isMobile" @click="toggleSidebar()"><fa :icon="['fas', 'arrow-left']" v-if="!openSidebar"></fa><fa :icon="['fas', 'arrow-right']" v-if="openSidebar"></fa></div><div class="pt-2 ps-1 mb-3 map-control-icon" v-if="isMobile" @click="toggleSidebar()"><fa :icon="['fas', 'bars']"></fa><span class="ms-2">Map Controls</span></div><div id="map-sidebar"><p class="section-title">Location Search <fa class="ms-2" :icon="['fas', 'circle-info']"> </fa><span class="hover-text">OpenStreetMaps is a community driven project and may not contain every address.</span></p><div class="form-floating mt-2 mb-2"><input class="form-control" type="text" placeholder="Search address" v-model="selectedAddress"><label>Address</label></div><p class="small form-error" v-if="addressError">Address not found</p><button class="btn btn-primary" @click="getAddressInfo()">Search</button><hr class="mt-4 mb-4"><p class="section-title">Probability</p><p>BETA: Determine the probability that you will encounter a Bigfoot based on proximity of previous sightings.</p><div class="form-check form-switch mt-2 mb-2"><input class="form-check-input" type="checkbox" v-model="showProbability" checked @change="toggleProbability()"></div><div class="mt-4 mb-2"><label>Radius</label><input class="form-range" type="range" placeholder="Radius" step="1" min="1" max="10" v-model="radius" @change="updateMarkerRadius()"><span class="small">{{metersToMiles()}} miles</span></div><hr class="mt-4 mb-4"><p class="section-title">Data Filters</p><div class="form-floating"><select class="form-select" id="form-year" v-model="selectedYear" @change="filterMap()"><option :value="null">All</option><option v-for="year in yearList" :value="year">{{year}}</option></select><label for="form-year">year</label></div><div class="form-floating mt-3"><select class="form-select" id="form-classification" v-model="selectedClass" @change="filterMap()"><option :value="null">All</option><option value="Class A">Class A</option><option value="Class B">Class B</option><option value="Class C">Class C</option></select><label for="form-classification">Class</label></div></div>