<div id="marker-info-sidebar" :class="[openMarkerSidebar ? 'active' : '']"><div class="marker-info-sidebar-content" v-if="selectedMarkerDetails"><div class="marker-info-sidebar-content-fixed"><div class="mb-4"><div class="d-flex"><div class="me-auto"><p class="lg-title mb-0">Report {{selectedMarkerDetails.number}}</p></div><div><div class="p-1" @click="closeMarkerSidebar()"><fa :icon="['fas', 'xmark']"></fa></div></div></div><p class="subtitle small"><span class="color-highlight">{{selectedMarkerDetails.county}}, {{selectedMarkerDetails.state}}</span></p></div></div><div class="marker-info-sidebar-content-scroll"><div class="mb-4"><div class="row"><div class="col-md border-right"><p class="section-title">Class Type</p><p><fa class="fa-star me-2" :icon="['fas', 'star']" v-if="selectedMarkerDetails.classification === 'Class A'"></fa><fa class="fa-star me-2" :icon="['fas', 'star-half-stroke']" v-if="selectedMarkerDetails.classification === 'Class B'"></fa><fa class="fa-star me-2" :icon="['far', 'star']" v-if="selectedMarkerDetails.classification === 'Class C'"></fa>{{selectedMarkerDetails.classification}}</p></div><div class="col-md"><p class="section-title">Accuracy</p><p><fa class="me-2" :icon="['far', 'thumbs-down']"></fa>TBD%</p></div></div></div><div class="mb-4"><p class="section-title">Details</p><p class="solid-bg-white"><fa class="me-2" :icon="['far', 'clock']">temperature-half</fa>{{formatDateTime(selectedMarkerDetails.timestamp)}}<br>{{selectedMarkerDetails.title}}</p></div><div class="mb-4"><p class="section-title">Weather</p><div class="solid-bg-white"> <div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['far', 'circle']"> </fa><span>Overview: </span></p><p>{{selectedMarkerDetails.summary}}</p></div></div><div class="solid-bg-white mt-2"><div><p class="section-title-2"><fa class="me-2" :icon="['fas', 'temperature-half']"> </fa><span>Temperature</span></p><div class="row mt-3"><div class="col-md"> <p class="section-title-3">Low: </p><p class="display-1-3">{{(selectedMarkerDetails.temperature_low).toFixed()}}&#8457;</p></div><div class="col-md"> <p class="section-title-3">Mid: </p><p class="display-1-3">{{(selectedMarkerDetails.temperature_mid).toFixed()}}&#8457;</p></div><div class="col-md"> <p class="section-title-3">High: </p><p class="display-1-3">{{(selectedMarkerDetails.temperature_high).toFixed()}}&#8457;</p></div></div></div></div><div class="row m-0"><div class="col-md ps-0 pe-1"><div class="solid-bg-white mt-2"><div><p class="section-title-2"><fa class="me-2" :icon="['fas', 'moon']"> </fa><span class="section-title-2">Moon Phase:</span></p><p class="display-1-5">{{selectedMarkerDetails.moon_phase}}</p></div></div></div><div class="col-md pe-0 ps-1"><div class="solid-bg-white mt-2"><div><p class="section-title-2"><fa class="me-2" :icon="['fas', 'cloud']"> </fa><span>Cloud Cover:</span></p><p class="display-1-5">{{selectedMarkerDetails.cloud_cover * 100}}%</p></div></div></div></div><div class="solid-bg-white mt-2"><div><p class="section-title-2"><fa class="me-2" :icon="['fas', 'wind']"> </fa><span>Wind</span></p><div class="row mt-3"><div class="col-md"><p class="section-title-3">Direction: </p><p>{{selectedMarkerDetails.wind_bearing}} {{getWindDirection(selectedMarkerDetails.wind_bearing)}}</p></div><div class="col-md"><p class="section-title-3">Speed: </p><p>{{selectedMarkerDetails.wind_speed}} MPH</p></div></div></div></div><div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['fas', 'cloud-rain']"> </fa><span>Precipitation</span></p><div class="row mt-3"><div class="col-md"><p class="section-title-3">Type: </p><p>{{selectedMarkerDetails.precip_type || 'None'}}</p></div><div class="col-md"><p class="section-title-3">Probability: </p><p>{{selectedMarkerDetails.precip_probability * 100}}%</p></div><div class="col-md"><p class="section-title-3">Intensity: </p><p>{{selectedMarkerDetails.precip_intensity}}</p></div></div></div></div><div class="row m-0"><div class="col-md ps-0 pe-1"><div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['fas', 'temperature-half']"> </fa><span> Dew Point: </span></p><p class="display-1-5">{{(selectedMarkerDetails.dew_point).toFixed()}}&#8457;</p></div></div></div><div class="col-md pe-0 ps-1"><div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['fas', 'droplet']"> </fa><span> Humidity: </span></p><p class="display-1-5">{{selectedMarkerDetails.humidity * 100}}% </p></div></div></div></div><div class="row m-0"><div class="col-md ps-0 pe-1"> <div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['fas', 'gem']"> </fa><span> Pressure: </span></p><p class="display-1-5">{{selectedMarkerDetails.pressure}}</p></div></div></div><div class="col-md pe-0 ps-1"><div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['fas', 'sun']"> </fa><span> UV Index: </span></p><p class="display-1-5">{{selectedMarkerDetails.uv_index}}&nbsp;<span v-if="selectedMarkerDetails.uv_index &gt;= 6">High</span><span v-if="selectedMarkerDetails.uv_index &lt;= 3 &amp;&amp; selectedMarkerDetails.uv_index &gt;= 5">Moderate</span><span v-if="selectedMarkerDetails.uv_index &lt;= 2">Low</span></p></div></div></div></div><div class="row m-0"><div class="col-md ps-0 pe-1"> <div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['fas', 'eye']"> </fa><span> Visibility: </span></p><p class="display-1-5">{{selectedMarkerDetails.visibility}} miles</p></div></div></div><div class="col-md pe-0 ps-1"><div class="solid-bg-white mt-2"><div class="mb-3"><p class="section-title-2"><fa class="me-2" :icon="['far', 'circle']"> </fa><span>Season: </span></p><p class="display-1-5">{{selectedMarkerDetails.season}}</p></div></div></div></div></div></div></div><div class="marker-info-sidebar-bg"></div></div>