<div class="card"><div class="card-header"><p class="chart-title">Tabular data</p><hr></div><div class="card-body"><div class="table-responsive"><DataTable class="table table-small table-striped table-hover"><thead><tr><th>number</th><th>title</th><th>classification</th><th>timestamp</th><th>latitude</th><th>longitude</th></tr></thead><tbody><tr v-for="sighting in sightingData"><td>{{ sighting.number }}</td><td>{{ sighting.title }}</td><td>{{ sighting.classification }}</td><td>{{ sighting.timestamp }}</td><td>{{ sighting.latitude }}</td><td>{{ sighting.longitude }}</td></tr></tbody></DataTable></div></div></div>