<div class="card"><div class="card-header"><div class="row justify-content-md-end"><div class="col-md"><p class="chart-title">Sightings By Weather</p></div><div class="col-md-6 group-by-filters"><span class="me-3 small">Group by:</span><span class="me-3" @click="updateGroupByValue('moon_phase')" :class="[groupByValue === 'moon_phase' ? 'active' : '']">Moon Phase</span><span class="me-3" @click="updateGroupByValue('cloud_cover')" :class="[groupByValue === 'cloud_cover' ? 'active' : '']">Cloud Cover</span><span @click="updateGroupByValue('season')" :class="[groupByValue === 'season' ? 'active' : '']">Season</span></div></div><hr class="mt-0"></div><div class="card-body"> <div class="chart" ref="plotPerTime"></div></div></div>