<div id="main-sidebar" :class="[openSidebar ? 'active' : '']"><div class="d-flex flex-column full-height" v-if="!isMobile"><div class="nav-content"><div id="logo-container"><img class="logo" src="../assets/images/BffLogoWhite.png" :class="[openSidebar ? 'active' : '']"><img class="logoIcon" src="../assets/images/BffLogoIconWhite.png" :class="[!openSidebar ? 'active' : '']"></div><div class="nav-link" @click="toggleSidebar()"><fa :icon="['fas', 'bars']"></fa></div><div class="site-navigation-links"><router-link class="nav-link" v-for="link in routerLinkList" :to="link.route"><fa :icon="link.icon"></fa><span class="ms-2 small">{{ link.name }}</span></router-link></div></div><div class="social mt-auto"><div class="mb-1" v-for="social in socialList"><a :href="social.link" target="_blank"><fa class="me-3" :icon="social.icon"></fa></a></div></div></div><div v-if="isMobile"><div class="d-flex flex-row"><div id="logo-container"><img class="logo active" src="../assets/images/BffLogoWhite.png"></div><div class="nav-link ms-auto" @click="toggleMobileMenu()"><fa class="p-3" :icon="['fas', 'bars']"></fa></div></div><div class="mobile-menu mt-3 mb-3" :class="[openMobileMenu ? 'active' : '']"><div class="links"><router-link class="nav-link" v-for="link in routerLinkList" :to="link.route"><fa :icon="link.icon"></fa><span class="ms-2 small">{{ link.name }}</span></router-link></div><div class="social mt-5"><a :href="social.link" target="_blank" v-for="social in socialList"><fa class="me-3" :icon="social.icon"></fa></a></div></div></div></div>