<div class="card"><div class="card-header"><div class="row justify-content-md-end"><div class="col-md"><p class="chart-title">Sighting Trends</p><hr></div></div></div><div class="card-body"><div class="table-responsive"><table class="table table-small table-striped table-hover"><thead><tr><th>State</th><th>Season</th><th>Temperature High</th><th>Temperature Low</th><th>Cloud Cover</th><th>Moon Phase</th><th>Precipitation</th></tr></thead><tbody><tr><td>{{bestConditions.state}}</td><td>{{bestConditions.season}}</td><td>{{(bestConditions.temperature_high).toFixed()}}&#8457;</td><td>{{(bestConditions.temperature_low).toFixed()}}&#8457;</td><td>{{bestConditions.cloud_cover}}%</td><td>{{bestConditions.moon_phase}}</td><td>{{bestConditions.precip_probability}}%</td></tr></tbody></table></div></div></div>