<div class="card"><div class="card-header"><p class="chart-title">Latest Sighting Activity</p><hr></div><div class="card-body"><div class="table-responsive"><table class="table table-small table-hover"><thead><tr><th>Identifier</th><th>Classification</th><th>Timestamp</th><th>State</th></tr></thead><tbody><tr v-for="sighting in reducedData"><td>{{ sighting.number }}</td><td>{{ sighting.classification }}</td><td>{{ formatDateTime(sighting.timestamp) }}</td><td>{{ sighting.state }}</td></tr></tbody></table></div></div></div>