<div>DataFilters</div><div class="row"><div class="col-md"><div class="form-floating mb-2"><select class="form-select" id="form-year" v-model="form.year"><option :value="null">All</option><option v-for="year in yearList" :value="year">{{year}}</option></select><label for="form-year">year</label></div></div><div class="col-md"><div class="form-floating mb-2"><select class="form-select" id="form-location" v-model="form.state"><option :value="null">All</option><option v-for="location in sightingLocationList" :value="location">{{location}}</option></select><label for="form-location">State/Providence</label></div></div><div class="col-md"><div class="form-floating mb-2"><select class="form-select" v-model="form.classification"><option :value="null">All</option><option value="Class A">Class A</option><option value="Class B">Class B</option><option value="Class C">Class C</option></select><label for="">Class</label></div></div><div class="col-md-2 mb-2"><button class="btn btn-primary btn-submit" @click="submitForm()">Submit</button></div></div>