<div class="card"><div class="card-header"><div class="row justify-content-md-end"><div class="col-md"><p class="chart-title">Sightings Over Time</p></div><div class="col-md-5 group-by-filters"><span class="me-3 small">Group by:</span><span class="me-3" @click="updateGroupByValue('year')" :class="[groupByValue === 'year' ? 'active' : '']">Year</span><span class="me-3" @click="updateGroupByValue('month')" :class="[groupByValue === 'month' ? 'active' : '']">Month</span><span @click="updateGroupByValue('day')" :class="[groupByValue === 'day' ? 'active' : '']">Day</span></div></div><hr class="mt-0"></div><div class="card-body"> <div class="chart" ref="plotPerTime"></div></div></div>