<InfoBox v-if="showProbability" :msg="'Geo Message | You have a '+probability+'% of seeing a Bigfoot based on your current location.'"></InfoBox><div id="bigfoot-map"><LMap ref="map" :zoom="zoom" :center="center" :options="mapOptions"><LTileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" layer-type="base" name="OpenStreetMap"></LTileLayer><LMarkerClusterGroup><LMarker v-for="sighting in sightingData" :lat-lng="[sighting.latitude, sighting.longitude]" @click="openMarkerSidebar(sighting)"><LTooltip :options="{ permanent: false, interactive: true }"><p>{{ sighting.title}}</p></LTooltip></LMarker></LMarkerClusterGroup><LLayerGroup ref="draggableGroup" v-if="showProbability"><LMarker :lat-lng="markerLatLng" :options="{draggable: true}" @dragend="onDragEnd"><LTooltip :options="{ permanent: true, interactive: true }"><p>Drag Me</p></LTooltip></LMarker><LCircle ref="circle" :lat-lng="markerLatLng" :options="circleOptions"></LCircle></LLayerGroup></LMap></div>